/* global document, Office */
/* Minimalny taskpane dla zgłaszania phishing — zbiera checkboxy, dołącza oryginalną wiadomość i wysyła EWS CreateItem */

let isSending = false; // <- zapobiega podwójnemu wysyłaniu
let isInitialized = false; // <- guard przed wielokrotną inicjalizacją UI

Office.onReady((info) => {
  try {
    if (isInitialized) return; // jeśli już zainicjowano — nic nie rób
    isInitialized = true;

    if (info.host === Office.HostType.Outlook) {
      const appBody = document.getElementById("app-body");
      if (appBody) appBody.style.display = "block";

      const item = Office.context.mailbox.item;
      const subjEl = document.getElementById("item-subject");
      if (subjEl) subjEl.textContent = (item && item.subject) ? item.subject : "(brak tematu)";

      // panel elements
      // try both ids to be robust (old/new)
      const tile = document.getElementById("reportSuspiciousBtn") || document.getElementById("runSendPost");
      const btnReport = document.getElementById("report-phish");
      const btnBack = document.getElementById("btn-back");
      const resultClose = document.getElementById("result-close");
      const resultAgain = document.getElementById("result-again");

      function showPanel(id) {
        // immediate show/hide without animations
        const panels = ["panel-select","panel-report","panel-result"];
        panels.forEach(pid => {
          const el = document.getElementById(pid);
          if (!el) return;
          if (pid === id) {
            el.classList.remove("hidden");
          } else {
            el.classList.add("hidden");
          }
        });
      }

      if (tile) {
        // attach handlers idempotently
        const onTileClick = () => showPanel("panel-report");
        const onTileKey = (e) => { if (e.key === "Enter" || e.key === " ") { e.preventDefault(); showPanel("panel-report"); } };
        tile.removeEventListener("click", onTileClick);
        tile.removeEventListener("keydown", onTileKey);
        tile.addEventListener("click", onTileClick);
        tile.addEventListener("keydown", onTileKey);
      }

      if (btnReport) {
        // ensure it is a button (prevents any default submit behavior)
        btnReport.type = "button";
        const onReportClick = () => {
          btnReport.disabled = true;
          // call and re-enable after completion
          Promise.resolve(sendPhishingReport()).finally(() => { btnReport.disabled = false; });
        };
        // idempotent attach
        btnReport.removeEventListener("click", onReportClick);
        btnReport.addEventListener("click", onReportClick);
      }
      if (btnBack) btnBack.addEventListener("click", () => showPanel("panel-select"));
      if (resultClose) {
        const onResultClose = () => {
          // prefer to close the taskpane container if supported
          if (Office && Office.context && Office.context.ui && typeof Office.context.ui.closeContainer === "function") {
            try {
              Office.context.ui.closeContainer();
              return;
            } catch (e) {
              // ignore and fallback
            }
          }
          // fallback: go to first panel
          showPanel("panel-select");
        };
        resultClose.removeEventListener("click", onResultClose);
        resultClose.addEventListener("click", onResultClose);
      }
      if (resultAgain) resultAgain.addEventListener("click", () => {
        showPanel("panel-report");
        document.querySelectorAll("#panel-report input[type='checkbox']").forEach(cb => cb.checked = false);
        const statusEl = document.getElementById("phish-status"); if (statusEl) statusEl.textContent = "";
      });

      // start on select panel
      showPanel("panel-select");
    }
  } catch (err) {
    console.error("Office.onReady handler error:", err);
  }
});

async function sendPhishingReport() {
  if (isSending) {
    console.warn("Już trwa wysyłanie — pomijam kolejne wywołanie");
    return;
  }
  isSending = true;

  try {
    const REPORT_ADDRESS = "soc@wroclaw.sa.gov.pl"; // <- ustaw właściwy adres
    const item = Office.context.mailbox.item;
    if (!item || !item.itemId) {
      setStatus("Brak aktywnej wiadomości.", true);
      return;
    }

    const opts = [
      { id: "opt-answered", text: "Odpowiedziano na e-mail." },
      { id: "opt-downloaded", text: "Pobrano plik." },
      { id: "opt-opened", text: "Otworzono załącznik." },
      { id: "opt-visited", text: "Odwiedzono link." },
      { id: "opt-password", text: "Wprowadzono swoje hasło." },
      { id: "opt-forwarded", text: "Przesłano dalej ten e-mail." },
      { id: "opt-none", text: "Nie zrobiono nic z e-mailem." }
    ];

    const selected = opts.filter(o => document.getElementById(o.id)?.checked).map(o => o.text);
    const selectionHtml = selected.length ? `<ul>${selected.map(s => `<li>${escapeXml(s)}</li>`).join("")}</ul>` : "<p>Brak zaznaczonych opcji.</p>";
    const reportSubject = `Zgłoszenie phishing — ${item.subject || ""}`;
    const reportBody = `<p>Użytkownik zgłosił podejrzaną wiadomość. Wybrane opcje:</p>${selectionHtml}<hr/><p>Oryginalny temat: ${escapeXml(item.subject || "(brak)")}</p>`;

    // uzyskaj EWS Id jeśli dostępne
    let ewsId = item.itemId;
    try {
      if (Office.context.mailbox.convertToEwsId) {
        ewsId = Office.context.mailbox.convertToEwsId(item.itemId);
      }
    } catch (e) {
      // fallback - użyj item.itemId
    }

    function makeEwsRequestPromise(soap) {
      return new Promise((resolve, reject) => {
        Office.context.mailbox.makeEwsRequestAsync(soap, (res) => {
          if (res.status === Office.AsyncResultStatus.Succeeded) {
            resolve(res.value);
          } else {
            reject(res.error || res);
          }
        });
      });
    }

    async function getMimeContentForItem(id) {
      const getItemReq = `<?xml version="1.0" encoding="utf-8"?>
        <soap:Envelope xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance"
                       xmlns:m="http://schemas.microsoft.com/exchange/services/2006/messages"
                       xmlns:t="http://schemas.microsoft.com/exchange/services/2006/types"
                       xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/">
          <soap:Header>
            <t:RequestServerVersion Version="Exchange2016" />
          </soap:Header>
          <soap:Body>
            <m:GetItem>
              <m:ItemShape>
                <t:BaseShape>IdOnly</t:BaseShape>
                <t:IncludeMimeContent>true</t:IncludeMimeContent>
              </m:ItemShape>
              <m:ItemIds>
                <t:ItemId Id="${escapeXml(id)}" />
              </m:ItemIds>
            </m:GetItem>
          </soap:Body>
        </soap:Envelope>`;

      const resp = await makeEwsRequestPromise(getItemReq);
      const m = resp.match(/<\w*:MimeContent[^>]*>([\s\S]*?)<\/\w*:MimeContent>/i);
      if (!m || !m[1]) {
        throw new Error("MimeContent nie znaleziono w odpowiedzi EWS.");
      }
      return m[1].trim();
    }

    async function createAndSendReport(mimeBase64) {
      const createReq = `<?xml version="1.0" encoding="utf-8"?>
        <soap:Envelope xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance"
                       xmlns:m="http://schemas.microsoft.com/exchange/services/2006/messages"
                       xmlns:t="http://schemas.microsoft.com/exchange/services/2006/types"
                       xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/">
          <soap:Header>
            <t:RequestServerVersion Version="Exchange2016" />
          </soap:Header>
          <soap:Body>
            <m:CreateItem MessageDisposition="SendAndSaveCopy">
              <m:SavedItemFolderId>
                <t:DistinguishedFolderId Id="sentitems" />
              </m:SavedItemFolderId>
              <m:Items>
                <t:Message>
                  <t:Subject>${escapeXml(reportSubject)}</t:Subject>
                  <t:Body BodyType="HTML">${escapeXml(reportBody)}</t:Body>
                  <t:ToRecipients>
                    <t:Mailbox>
                      <t:EmailAddress>${REPORT_ADDRESS}</t:EmailAddress>
                    </t:Mailbox>
                  </t:ToRecipients>
                  <t:Attachments>
                    <t:FileAttachment>
                      <t:Name>SuspectedMessage.eml</t:Name>
                      <t:ContentType>message/rfc822</t:ContentType>
                      <t:Content>${mimeBase64}</t:Content>
                    </t:FileAttachment>
                  </t:Attachments>
                </t:Message>
              </m:Items>
            </m:CreateItem>
          </soap:Body>
        </soap:Envelope>`;

      const resp = await makeEwsRequestPromise(createReq);
      return resp;
    }

    setStatus("Wysyłanie zgłoszenia...", false);

    // wykonaj sekwencję: GetItem -> CreateItem
    const mimeBase64 = await getMimeContentForItem(ewsId);
    await createAndSendReport(mimeBase64);

    // pokaż panel z sukcesem
    showResult(true, "Dziękujemy — zgłoszenie zostało wysłane.", "Twoje zgłoszenie zostało przekazane zespołowi ds. bezpieczeństwa.");
  } catch (err) {
    console.error("EWS error:", err);
    const msg = (err && err.message) ? err.message : JSON.stringify(err);
    showResult(false, "Błąd wysyłania zgłoszenia", "Wystąpił błąd: " + escapeXml(msg));
  } finally {
    isSending = false;
  }

  function setStatus(msg, isError) {
    const statusEl = document.getElementById("phish-status");
    if (!statusEl) return;
    statusEl.textContent = msg;
    statusEl.style.color = isError ? "#b00020" : "#2b7a0b";
  }
}

// simplified showResult — immediate show/hide (no animation)
function showResult(success, title, message) {
  const resultTitle = document.getElementById("result-title");
  const resultMessage = document.getElementById("result-message");
  const resultIcon = document.getElementById("result-icon");
  const resultAgain = document.getElementById("result-again");

  if (resultTitle) {
    resultTitle.textContent = title;
    resultTitle.style.textAlign = "center";
  }
  if (resultMessage) resultMessage.innerHTML = message;

  if (resultIcon) {
    if (success) {
      resultIcon.innerHTML = '<svg width="64" height="64" viewBox="0 0 64 64" xmlns="http://www.w3.org/2000/svg" role="img" aria-hidden="true"><circle cx="32" cy="32" r="32" fill="#c6f6e9"/><circle cx="32" cy="32" r="22" fill="#a8efd0"/><path d="M20 34 L28 42 L44 24" stroke="#065f3a" stroke-width="4" fill="none" stroke-linecap="round" stroke-linejoin="round"/></svg>';
    } else {
      resultIcon.innerHTML = '<svg width="64" height="64" viewBox="0 0 64 64" xmlns="http://www.w3.org/2000/svg" role="img" aria-hidden="true"><circle cx="32" cy="32" r="32" fill="#ffd9d9"/><circle cx="32" cy="32" r="22" fill="#ffd0d0"/><path d="M22 22 L42 42 M42 22 L22 42" stroke="#a11a1a" stroke-width="4" stroke-linecap="round" stroke-linejoin="round"/></svg>';
    }
  }
  if (resultAgain) {
    if (success) resultAgain.classList.add("hidden"); else resultAgain.classList.remove("hidden");
  }

  // immediate show/hide of panels
  const panels = ["panel-select","panel-report","panel-result"];
  panels.forEach(pid => {
    const el = document.getElementById(pid);
    if (!el) return;
    if (pid === "panel-result") el.classList.remove("hidden"); else el.classList.add("hidden");
  });
}

function escapeXml(s) {
  return (!s) ? "" : s.replace(/[<>&'"]/g, c => ({ "<":"&lt;", ">":"&gt;", "&":"&amp;", "'":"&apos;", '"':"&quot;" }[c]));
}
